import { FETCH_DATA } from "../actions/types";
export default (state = [], action) => {
    switch (action.type) {
        case FETCH_DATA:
            console.log(action.payload, "action.payload");

            return action.payload;
        default:
            console.log(state, "default");
            return state;
    }
};



